body {
  margin: 0;
  font-family: 'Nunito', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 100vw;
  background-color: #13131a;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
box-sizing: border-box;
margin: 0;
padding: 0;
font-family: 'PT Sans', sans-serif;
}

:root {
    /*custom properties that contatin specific values to be reused throughout HTML document; accessed with the var() function*/
    --main-color: #f5b562; 
    --black: #13131a;
    --bg: #82828b;
    --footer-bg: #3b3b4783;
    --text: #fff;
    --hover: #ec969f;
    --border: .1rem solid rgba(255, 255, 255, .3); /*rem unit allows you to define a root em unit value on the html element,
    then use values on subsequent elements that are relative to the specified root unit; rem will also scale specific page elements while leaving
    others unaffected*/
}

* {
  font-family: 'Roboto', sans-serif;
}

header {
    position: -webkit-sticky; /*included this css property to include Safari IOS 6.1+ devices*/
    position: sticky;
    top:0;
    z-index: 10;
    background-color: #13131a;
    background-color: var(--black);
    color: #333f48;
    display: inline-flex;
    width: 100%;
    margin: 0;
    height: 8vh;
    clear: both;
    border-bottom: 1px solid #333f48;
    box-sizing: border-box;
} 

.header-link {
    margin: auto 0px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    color: #333f48;
    text-decoration: none;
}

.header-link:nth-child(1) {
    display: inline-flex;
}

#header-container {
    /*to support Firefox 3+, Firefox for Android 4+*/
    width: -moz-fit-content;
    width: -webkit-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin: auto 30px;
    float: left;
}

#header-logo, h1 {
    margin: auto 5px;
    flex-direction: row;
    flex-wrap: nowrap;
    flex: auto;
}

#header-logo {
    width: 36px;
    height: 36px;
    background-image: url(/Shopify-Internship-Image-Repository/static/media/repo-logo.ef056746.png);
    background-color: #13131a;
    background-color: var(--black);
    background-size: 36px 36px;
    background-position: center;
    background-repeat: no-repeat;
}

h1 {
    font-size: 28px;
    color: rgba(255, 255, 255, 0.794);
}

h1:hover {
    color: #f5b562;
    color: var(--main-color);
}

nav {
    display: inline-flex;
    justify-content: right;
    width: -moz-fit-content; /*to support Firefox 3+, Firefox for Android 4+*/
    width: -webkit-fit-content;
    width: fit-content;
    margin: auto 20px auto auto;
    float: left;
}

nav > .header-link {
    margin: auto 20px;
    flex-direction: row;
    flex-wrap: nowrap;
    flex: auto;
    font-size: larger;
    color: rgba(255, 255, 255, 0.794);
}

nav > .header-link:hover {
    color: #ec969f;
    color: var(--hover);
}
#home_container {
    max-width: 100%;
    width: 100%;
    display: flexbox;
    margin: 0px;
    z-index: -10;
    top:0;
}

#home_about {
    background: url(/Shopify-Internship-Image-Repository/static/media/redsetup.9587bfa0.jpg) center center/cover 
    no-repeat;
    /* background: gray; */
    /* height: 100vh; */
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
    object-fit: contain;
}

.home_content {
    width: 100%;
    text-align: left;
    padding: 100px 0px 40px 0px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    flex-direction: column;
    flex-wrap: wrap;
}

.home_content:nth-of-type(2n+1) {
    background-color: #f8971f;
    color: white;
}

.home_content:nth-of-type(2n) {
    background-color: white;
    color: #333f48;
}

.home_content > h1 {
    font-size: 48px;
    margin: -50px 100px 0px 100px;
    color: rgb(245, 182, 80);
    font-weight: lighter;
}

.home_content > h1:nth-child(1) {
    text-align: center;
}

.home_content > p {
    font-size: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #c8a5a5;
    font-style: italic;
}

.home_content > div {
    margin: 0px 100px;
}

.home_content:nth-child(1) {
    padding-bottom: 0px;
}





/* TABLE CSS */
#tableContainer {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    display: flex;
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: 0px auto;
}

#tableContainer > h4 {
    text-align: center;
    margin: 0px auto;
}

#homeCheckout {
    margin: 5px auto 0px auto;
}

#checkout {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: none;
    outline: none;
    background-color: #f8971f;
    color: white;
    padding: 10px;
    font-size: 2vw;
    margin: 0px auto;
    cursor: pointer;
}

#checkout:hover {
    background-color: #bf5700;
    transition: 0.2s;
}


/* Team Information */
#profile-container {
    display: flex;
    flex-direction: column;
    max-width: 75%;
    margin: 0px auto;
}

.profile {
    display: flex;
    flex-direction: row;
    margin: 25px auto;
}

.profile-pic {
    width: 60px;
    height: 60px;
    background-color: white;
    margin: auto 0px;
    border-radius: 50%;
    background-image: url(/Shopify-Internship-Image-Repository/static/media/user.ec735cbd.svg);
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60px 60px;
    cursor: pointer;
}

.profile-pic:nth-child(odd) {
    margin-right: 10px;
}

.profile-pic:nth-child(even) {
    margin-left: 10px;
}

.profile-desc {
    font-size: 20px;
    margin: auto 0px;
}

.profile-desc:nth-child(odd) {
    margin-right: 20px;
}

.profile-desc:nth-child(even) {
    margin-left: 20px;
}

.profile-linked {
    z-index: 10;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    background-image: none;
    background-position: center;
    background-repeat: no-repeat;
    background-size: 60px 60px;
}

.profile-linked:hover {
    background-image: url(/Shopify-Internship-Image-Repository/static/media/linkedin.9db27756.svg);
    transition: 0.3s;
    background-color: rgba(255, 255, 255, 0.75);
}
